import React from 'react'
import SiteContent from '../../assets/SiteContent.json'

const Page10 = () => {
    return (
        <>
            <h3 className="title tablet-title">{SiteContent.page10.title}</h3>
            <div className="image-container">
                <div className="image" style={{
                    backgroundImage: `url(./assets/${SiteContent.page10.image}.jpeg)`
                }}></div>
            </div>
            <div className="info">
                <h3 className="title desktop-title">{SiteContent.page10.title}</h3>
                <p>Thank you for reading the content i hope it was helpful, This site was created by <a href="https://yassenr.herokuapp.com/" target="_target">Yassen Rabie</a> and it's based on an article from <a href="https://icatcare.org/" target="_target">international cat care</a> site feel free to read the original <a href="https://icatcare.org/advice/thinking-of-getting-a-cat/" target="_target">article</a> if you need more information.</p>
            </div>
        </>
    )
}

export default Page10
